<template>
  <div id="cameraPostionEdit" v-loading="isLoging">
    <el-dialog
      title="摄像头位置信息"
      width="40%"
      top="2rem"
      :close-on-click-modal="false"
      :visible.sync="showDialog"
      :destroy-on-close="true"
      @close="close()"
    >
      <div id="shared" style="margin-top: 1rem;margin-right: 100px;">
        <el-form ref="form" :rules="rules" :model="form" label-width="200px" >
          <el-form-item label="名称" prop="name">
            <el-input  v-model="form.name" clearable></el-input>
          </el-form-item>


          <el-form-item>
            <div style="float: right;">
              <el-button type="primary" @click="onSubmit" >确认</el-button>
              <el-button @click="close">取消</el-button>
            </div>

          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "cameraPostionEdit",
  props: {},
  computed: {},
  created() {},
  data() {
    return {
      pid:null,
      listChangeCallback: null,
      showDialog: false,
      isLoging: false,
      hostNames:[],
      mediaServerList: [], // 滅体节点列表
      form: {},
      isEdit: false,
      rules: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
      },
    };
  },
  methods: {
    openDialog: function (row, callback,pid) {
      console.log(row)
      this.showDialog = true;
      this.isEdit = false;
      if (row) {
        this.isEdit = true;
      }
      this.form = {};
      this.listChangeCallback = callback;
      if (row != null) {
        this.form = row;
      }
      if(pid){
        this.pid = pid;
      }
    },
    onSubmit: function () {
      if(this.pid){
        this.form.pid = this.pid;
      }
      this.$axios({
        method: 'post',
        url:`/api/camera/${this.isEdit?'update':'add'}/`,
        data: this.form
      }).then((res) => {
        console.log(res.data)
        if (res.data.code === 0) {
          this.listChangeCallback()
        }else {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: "error",
          });
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    close: function () {
      this.showDialog = false;
    },
  },
};
</script>



