<template>
  <div id="devicePosition" style="width:100vw; height: 91vh">
    <el-container v-loading="loading" style="height: 91vh;" element-loading-text="拼命加载中">
      <el-aside width="300px" style="background-color: #ffffff">
        <DeviceTree :clickEvent="clickEvent" :contextMenuEvent="contextMenuEvent"></DeviceTree>
      </el-aside>
      <el-container>
        <el-header height="50px" style="text-align: left;font-size: 17px;line-height:50px">
          分屏:
          <i class="el-icon-full-screen btn" :class="{active:spilt==1}" @click="spilt=1"/>
          <i class="el-icon-menu btn" :class="{active:spilt==4}" @click="spilt=4"/>
          <i class="el-icon-s-grid btn" :class="{active:spilt==9}" @click="spilt=9"/>
          <i class="el-icon-s-grid btn" :class="{active:spilt==16}" @click="spilt=16"/>

          <el-button size="mini" style="margin-right: 1rem;" type="primary" @click="savePositions">保存布局</el-button>
          <el-button size="mini" style="margin-right: 1rem;" type="primary" @click="deletePosition">删除</el-button>
          <el-button size="mini" style="margin-right: 1rem;" type="primary" @click="startAll">全部播放</el-button>
          <el-button size="mini" style="margin-right: 1rem;" type="primary" @click="stopAll">全部暂停</el-button>
          <el-button size="mini" style="margin-right: 1rem;" type="primary" @click="playAll">一键预览</el-button>
          <el-button size="mini" style="margin-right: 1rem;" type="primary" @click="removeAll">一键关闭预览</el-button>
        </el-header>
        <el-main style="padding: 0;">
          <div :style="divStyle">
            <div v-for="i in spilt" :key="i" class="play-box"
                 :style="liveStyle" :class="{redborder:playerIdx == (i-1)}"
                 @click="playerIdx = (i-1)">
              <div v-if="!videoUrl[i-1]" style="color: #ffffff;font-size: 30px;font-weight: bold;">{{ i }}</div>
              <player ref="player" v-else :videoUrl="videoUrl[i-1]" fluent autoplay @screenshot="shot"
                      @destroy="destroy"/>
            </div>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import uiHeader from "../layout/UiHeader.vue";
import player from './common/jessibuca_live.vue'
import DeviceTree from './common/DeviceTree2.vue'
import DeviceService from "./service/CameraService.js";
export default {
  name: "live",
  components: {
    uiHeader, player, DeviceTree
  },
  data() {
    return {
      deviceService: new DeviceService(),
      videoUrl: [''],
      ids:[],
      spilt: 1,//分屏
      playerIdx: 0,//激活播放器
      playIdx:0,
      updateLooper: 0, //数据刷新轮训标志
      count: 15,
      total: 0,
      height:0,
      width:0,
      //channel
      loading: false
    };
  },
  mounted() {

  },
  created() {
    //this.checkPlayByParam()
    this.getPosition();
  },

  computed: {
    divStyle(){
      let height = document.documentElement.clientHeight-140;
      let width =  document.documentElement.clientWidth-340;
      if(height<0){
        height = 0;
      }
      if(width<0){
        width = 0;
      }
      let style = {width: width+'px', height: height+'px',display:'flex','flex-wrap':'wrap','background-color':'#000'};
      // width: 99%;height: 99%;display: flex;flex-wrap: wrap;background-color: #000;

      return style;
    },
    liveStyle() {
      let width =  document.documentElement.clientWidth-340;
      if(width<0){
        width = 0;
      }
      let height = document.documentElement.clientHeight-140;
      if(height<0){
        height = 0;
      }
      console.log("height:"+height);
      let style = {width: '100%', height: '100%'}
      switch (this.spilt) {
        case 4:
          style = {width: (width/2-4)+'px', height: (height/2-4)+'px'}
          break
        case 9:
          style = {width: (width/3-4)+'px', height: (height/3-4)+'px'}
          break
        case 16:
          style = {width: (width/4-4)+'px', height: (height/4-4)+'px'}
          break
      }
      this.$nextTick(() => {
        for (let i = 0; i < this.spilt; i++) {
          const player = this.$refs.player
          player && player[i] && player[i].updatePlayerDomSize()
        }
      })
      return style
    }
  },
  watch: {
    spilt(newValue) {
      console.log("切换画幅;" + newValue)
      let that = this
      for (let i = 1; i <= newValue; i++) {
        if (!that.$refs['player' + i]) {
          continue
        }
        this.$nextTick(() => {
          if (that.$refs['player' + i] instanceof Array) {
            that.$refs['player' + i][0].resize()
          } else {
            that.$refs['player' + i].resize()
          }
        })

      }
      window.localStorage.setItem('split', newValue)
    },
    '$route.fullPath': 'checkPlayByParam'
  },
  destroyed() {
    clearTimeout(this.updateLooper);
  },
  methods: {
    playAll(){
      this.spilt = 16;
      let that = this;
      this.deviceService.getAllDeviceChannelList((data)=>{
        that.playIdx = 0;
        if (data.data.length > 0) {
          that.playIndex(data.data,that.playIndex,that);
        }
      }, (list)=>{
        console.log("设备加载完成")
      }, (error)=>{

      })
    },
    playIndex(datas,callbak,that){
      if(that.playIdx>=datas.length){
        console.log(that.videoUrl,that.ids);
        that.savePositions();
        return;
      }
      let item = datas[that.playIdx];
      that.$axios.get('/api/play/start/' + item.deviceId + '/' + item.channelId)
      .then(function (res) {
        that.playIndex2(res,that,item.id);
        that.playIdx++;
        callbak(datas,callbak,that);
      }).catch(function (e) {
      }).finally(() => {

      });
    },
    playIndex2(res,that,id){
        console.log(that.playIdx,res,id);
        if (res.data.code === 0 && res.data.data) {
          let videoUrl;
          if (location.protocol === "https:") {
            videoUrl = res.data.data.wss_flv;
          } else {
            videoUrl = res.data.data.ws_flv;
          }
          that.$set(that.ids, that.playIdx, id);
          that.setPlayUrl(videoUrl, that.playIdx);
        } else {
          that.$message.error(res.data.msg);
        }
    },
    removeAll(){
      this.videoUrl = [];
      this.ids = [];
      this.playerIdx = 0;
      this.savePositions();
    },
    startAll(){
        console.log(this.videoUrl);
        let players = this.$refs['player'];
        for(let i=0;i<this.spilt;i++){
            let url = this.videoUrl[i];
            if(url){
              this.$refs['player'][i].play(url);
            }
        }

    },
    stopAll(){
      let players = this.$refs['player'];
      players.forEach(item=>{
        console.log(item);
        item.pause();
      })
    },
    deletePosition(){
      this.$set(this.videoUrl, this.playerIdx, null);
      this.$set(this.ids, this.playerIdx, null);
      let _this = this;
      setTimeout(() => {
        window.localStorage.setItem('videoUrl', JSON.stringify(_this.videoUrl))
      }, 100)
    },
    getPosition(){
      this.$axios({
        method: 'get',
        url:`/api/live/position/`
      }).then((res) => {
        console.log(res.data)
        if (res.data.code === 0) {
          let result = res.data.data;
          this.spilt = result.spilt;
          this.videoUrl = result.videoUrl;
          this.ids = result.ids;
        }else {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: "error",
          });
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    savePositions(){
      let data = {};
      data.videoUrl = this.videoUrl;
      data.ids = this.ids;
      data.spilt = this.spilt;
      this.$axios({
        method: 'post',
        url:`/api/live/position/`,
        data: data
      }).then((res) => {
        console.log(res.data)
        if (res.data.code === 0) {
          this.$message({
              showClose: true,
              message: "保存成功",
              type: "success",
          });
        }else {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: "error",
          });
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    destroy(idx) {
      console.log(idx);
      this.clear(idx.substring(idx.length - 1))
    },
    clickEvent: function (device, data, isCatalog) {
      console.log(device, data, isCatalog);
      if (data.channelId && !isCatalog) {
        if (!device.status) {
          this.$message.error('设备离线!不允许点播');
        }else {
          this.sendDevicePush(data)
        }
      }
    },
    contextMenuEvent: function (device, event, data, isCatalog) {

    },
    //通知设备上传媒体流
    sendDevicePush: function (itemData) {
      // if (itemData.status === 0) {
      //   this.$message.error('设备离线!');
      //   return
      // }
      this.save(itemData)
      let deviceId = itemData.deviceId;
      // this.isLoging = true;
      let channelId = itemData.channelId;
      console.log("通知设备推流1：" + deviceId + " : " + channelId);
      let idxTmp = this.playerIdx
      let that = this;
      this.loading = true
      this.$axios({
        method: 'get',
        url: '/api/play/start/' + deviceId + '/' + channelId
      }).then(function (res) {
        if (res.data.code === 0 && res.data.data) {
          let videoUrl;
          if (location.protocol === "https:") {
            videoUrl = res.data.data.wss_flv;
          } else {
            videoUrl = res.data.data.ws_flv;
          }
          itemData.playUrl = videoUrl;
          that.$set(that.ids, idxTmp, itemData.id);
          console.log(that.ids);
          that.setPlayUrl(videoUrl, idxTmp);
        } else {
          that.$message.error(res.data.msg);
        }
      }).catch(function (e) {
      }).finally(() => {
        that.loading = false
      });
    },
    setPlayUrl(url, idx) {
      this.$set(this.videoUrl, idx, url)
      let _this = this
      setTimeout(() => {
        window.localStorage.setItem('videoUrl', JSON.stringify(_this.videoUrl))
      }, 100)

    },
    checkPlayByParam() {
      let {deviceId, channelId} = this.$route.query
      if (deviceId && channelId) {
        this.sendDevicePush({deviceId, channelId})
      }
    },
    shot(e) {
      // console.log(e)
      // send({code:'image',data:e})
      var base64ToBlob = function (code) {
        let parts = code.split(';base64,');
        let contentType = parts[0].split(':')[1];
        let raw = window.atob(parts[1]);
        let rawLength = raw.length;
        let uInt8Array = new Uint8Array(rawLength);
        for (let i = 0; i < rawLength; ++i) {
          uInt8Array[i] = raw.charCodeAt(i);
        }
        return new Blob([uInt8Array], {
          type: contentType
        });
      };
      let aLink = document.createElement('a');
      let blob = base64ToBlob(e); //new Blob([content]);
      let evt = document.createEvent("HTMLEvents");
      evt.initEvent("click", true, true); //initEvent 不加后两个参数在FF下会报错  事件类型，是否冒泡，是否阻止浏览器的默认行为
      aLink.download = '截图';
      aLink.href = URL.createObjectURL(blob);
      aLink.click();
    },
    save(item) {
      let dataStr = window.localStorage.getItem('playData') || '[]'
      let data = JSON.parse(dataStr);
      data[this.playerIdx] = item
      window.localStorage.setItem('playData', JSON.stringify(data))
    },
    clear(idx) {
      let dataStr = window.localStorage.getItem('playData') || '[]'
      let data = JSON.parse(dataStr);
      data[idx - 1] = null;
      console.log(data);
      window.localStorage.setItem('playData', JSON.stringify(data))
    },
  }
};
</script>
<style>
.btn {
  margin: 0 10px;

}

.btn:hover {
  color: #409EFF;
}

.btn.active {
  color: #409EFF;

}

.redborder {
  border: 2px solid red !important;
}

.play-box {
  background-color: #000000;
  border: 2px solid #505050;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
<style>
.videoList {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.video-item {
  position: relative;
  width: 15rem;
  height: 10rem;
  margin-right: 1rem;
  background-color: #000000;
}

.video-item-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
}

.video-item-img:after {
  content: "";
  display: inline-block;
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 3rem;
  height: 3rem;
  background-image: url("../assets/loading.png");
  background-size: cover;
  background-color: #000000;
}

.video-item-title {
  position: absolute;
  bottom: 0;
  color: #000000;
  background-color: #ffffff;
  line-height: 1.5rem;
  padding: 0.3rem;
  width: 14.4rem;
}

.baidumap {
  width: 100%;
  height: 100%;
  border: none;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

/* 去除百度地图版权那行字 和 百度logo */
.baidumap > .BMap_cpyCtrl {
  display: none !important;
}

.baidumap > .anchorBL {
  display: none !important;
}
</style>



