<template>
  <div id="app" style="width: 100%">
    <div class="page-header">
      <div class="page-title">报警列表</div>
      <div class="page-header-btn">
        <span>报警类型:</span>
        <el-select v-model="selectType" placeholder="请选择报警类型">
          <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
        </el-select>

        <el-date-picker
              v-model="daterange"
              type="daterange"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
        <el-button icon="el-icon-search" circle size="mini" :loading="getEventListLoading" @click="getEventList()"></el-button>
        <!-- <el-button icon="el-icon-refresh-right" circle size="mini" :loading="getEventListLoading"
                   @click="getEventList()"></el-button> -->
      </div>
    </div>
    <!--设备列表-->
    <el-table :data="eventList" style="width: 100%;font-size: 12px;" :height="winHeight" header-row-class-name="table-header">
      <el-table-column prop="deviceId" label="设备编号" min-width="200" >
      </el-table-column>
      <el-table-column prop="channelId" label="通道编号" min-width="200">
      </el-table-column>
      <el-table-column prop="position" label="位置" min-width="200">
      </el-table-column>
      <el-table-column prop="cameraName" label="名称" min-width="200" >
      </el-table-column>
      <el-table-column prop="alarmMethod" label="报警方式" min-width="200" >
          <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
              <el-tag v-if="scope.row.alarmMethod==1" size="medium">电话报警</el-tag>
              <el-tag v-if="scope.row.alarmMethod==2" size="medium">设备报警</el-tag>
              <el-tag v-if="scope.row.alarmMethod==3" size="medium">短信报警</el-tag>
              <el-tag v-if="scope.row.alarmMethod==4" size="medium">GPS报警</el-tag>
              <el-tag v-if="scope.row.alarmMethod==5" size="medium">视频报警</el-tag>
              <el-tag v-if="scope.row.alarmMethod==6" size="medium">设备故障报警</el-tag>
              <el-tag v-if="scope.row.alarmMethod==7" size="medium">其他报警</el-tag>
            </div>
          </template>
      </el-table-column>
      <el-table-column prop="alarmType" label="报警类型" min-width="200" >
          <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
              <span v-if="scope.row.alarmMethod==2">
                <el-tag v-if="scope.row.alarmType==1" size="medium">视频丢失报警</el-tag>
                <el-tag v-if="scope.row.alarmType==2" size="medium">设备防拆报警</el-tag>
                <el-tag v-if="scope.row.alarmType==3" size="medium">存储设备磁盘满报警</el-tag>
                <el-tag v-if="scope.row.alarmType==4" size="medium">设备高温报警</el-tag>
                <el-tag v-if="scope.row.alarmType==5" size="medium">设备低温报警</el-tag>
              </span>
              <span v-if="scope.row.alarmMethod==5">
                <el-tag v-if="scope.row.alarmType==1" size="medium">人工视频报警</el-tag>
                <el-tag v-if="scope.row.alarmType==2" size="medium">运动目标检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==3" size="medium">遗留物检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==4" size="medium">物体移除检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==5" size="medium">绊线检测报警</el-tag>

                <el-tag v-if="scope.row.alarmType==6" size="medium">入侵检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==7" size="medium">逆行检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==8" size="medium">徘徊检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==9" size="medium">流量统计报警</el-tag>
                <el-tag v-if="scope.row.alarmType==10" size="medium">密度检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==11" size="medium">视频异常检测报警</el-tag>
                <el-tag v-if="scope.row.alarmType==12" size="medium">快速移动报警</el-tag>
              </span>
              <span v-if="scope.row.alarmMethod==6">
                <el-tag v-if="scope.row.alarmType==1" size="medium">存储设备磁盘故障报警</el-tag>
                <el-tag v-if="scope.row.alarmType==2" size="medium">存储设备风扇故障报警</el-tag>
              </span>

            </div>
          </template>
      </el-table-column>
      <el-table-column prop="alarmTime" label="报警时间" min-width="200" >
      </el-table-column>
      <!-- <el-table-column prop="alarmDescription" label="报警内容描述" min-width="200" >
      </el-table-column> -->



      <el-table-column label="操作" min-width="100" fixed="right">
        <template slot-scope="scope">
          <el-button size="medium" v-bind:disabled="false" icon="el-icon-video-camera" type="text" @click="queryRecords(scope.row)">设备录像
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="float: right"
      @size-change="handleSizeChange"
      @current-change="currentChange"
      :current-page="currentPage"
      :page-size="count"
      :page-sizes="[15, 25, 35, 50]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    <deviceEdit ref="deviceEdit"></deviceEdit>
    <syncChannelProgress ref="syncChannelProgress"></syncChannelProgress>
  </div>
</template>

<script>
import uiHeader from '../layout/UiHeader.vue'
import deviceEdit from './dialog/deviceEdit.vue'
import syncChannelProgress from './dialog/SyncChannelProgress.vue'
import moment  from 'moment'
export default {
  name: 'eventlist',
  components: {
    uiHeader,
    deviceEdit,
    syncChannelProgress,
  },
  data() {
    return {
      options: [{
                value: 'a6',
                label: '入侵检测报警'
              }, {
                value: 'a2',
                label: '运动目标检测报警'
              }, {
                value: '',
                label: '全部'
              }],
      selectType:'',
      daterange: [],
      eventList: [], //设备列表
      updateLooper: 0, //数据刷新轮训标志
      winHeight: window.innerHeight - 200,
      currentPage: 1,
      count: 15,
      total: 0,
      getEventListLoading: false,
    };
  },
  computed: {

  },
  mounted() {
    this.initData();
    //this.updateLooper = setInterval(this.initData, 10000);
  },
  destroyed() {
    this.$destroy('videojs');
    clearTimeout(this.updateLooper);
  },
  methods: {
    initData: function () {
      this.getEventList();
    },
    currentChange: function (val) {
      this.currentPage = val;
      this.getEventList();
    },
    handleSizeChange: function (val) {
      this.count = val;
      this.getEventList();
    },
    getEventList: function () {
      this.getEventListLoading = true;
      console.log(this.daterange);
      var params =  {
          page: this.currentPage,
          count: this.count
        };
      if(this.daterange.length>0){
          params["startTime"]=this.daterange[0]+" 00:00:00";
          params["endTime"]=this.daterange[1]+" 23:59:59";
      }
      if(this.selectType!=''&&this.selectType){
          params["alarmMethod"]="5";
          if(this.selectType=="a6"){
            params["alarmType"]="6";
          }else if(this.selectType=="a2"){
            params["alarmType"]="2";
          }

      }
      this.$axios({
        method: 'get',
        url: `/api/alarm/all`,
        params: params
      }).then( (res)=> {
        if (res.data.code === 0) {
          this.total = res.data.data.total;
          this.eventList = res.data.data.list;
        }
        this.getEventListLoading = false;
      }).catch( (error)=> {
        console.error(error);
        this.getEventListLoading = false;
      });
    },
    queryRecords: function (itemData) {
      let deviceId = itemData.deviceId;
      let channelId = itemData.channelId;
      let chooseDate = moment(itemData.alarmTime).format("YYYY-MM-DD");
      let alarmTime = moment(itemData.alarmTime).format("HH:mm:ss");
      this.$router.push(`/gbRecordDetail2/${deviceId}/${channelId}/${chooseDate}/${alarmTime}/`)
    },


  }
};
</script>

<style>
.videoList {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.video-item {
  position: relative;
  width: 15rem;
  height: 10rem;
  margin-right: 1rem;
  background-color: #000000;
}

.video-item-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
}

.video-item-img:after {
  content: "";
  display: inline-block;
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 3rem;
  height: 3rem;
  background-image: url("../assets/loading.png");
  background-size: cover;
  background-color: #000000;
}

.video-item-title {
  position: absolute;
  bottom: 0;
  color: #000000;
  background-color: #ffffff;
  line-height: 1.5rem;
  padding: 0.3rem;
  width: 14.4rem;
}

</style>



