<template>
    <div id="app" style="width: 100%">
        <div class="page-header">
            <div class="page-title">摄像头配置</div>
            <div class="page-header-btn">
                <el-button
                  type="primary"
                  icon="el-icon-sort"
                  size="mini"
                  @click="toggleExpandAll"
                >展开/折叠</el-button>
                <el-button icon="el-icon-plus" size="mini" style="margin-right: 1rem;" type="primary" @click="handleAddPosition">添加位置
                </el-button>


                <el-input @input="getCameraList" style="margin-right: 1rem; width: auto;" size="mini" placeholder="名称"
                          prefix-icon="el-icon-search" v-model="searchName" clearable></el-input>
                <el-button icon="el-icon-refresh-right" circle size="mini" :loading="getCameraListLoading"
                    @click="getCameraList()"></el-button>
            </div>
        </div>
        <!--设备列表-->
        <el-table
          v-if="refreshTable"
          v-loading="getCameraListLoading"
          :data="cameraList"
          row-key="id"
          :default-expand-all="isExpandAll"
          :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
        >
          <el-table-column prop="name" label="名称" ></el-table-column>
          <el-table-column prop="deviceId" label="设备国标编号" ></el-table-column>
          <el-table-column prop="channelId" label="通道国标编号" ></el-table-column>
          <el-table-column prop="status" label="在线状态" >
            <template slot-scope="scope">
              <span v-if="scope.row.pid!=0 && scope.row.status">
                      在线
              </span>
              <span v-if="scope.row.pid!=0 && !scope.row.status">
                      离线
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="play" label="播放" >
            <template slot-scope="scope">
                <el-button size="medium" v-if="scope.row.pid!=0 && scope.row.status" icon="el-icon-video-play" type="text" @click="sendDevicePush(scope.row)">播放</el-button>
                <el-button size="medium" v-if="scope.row.pid!=0 && scope.row.status&&scope.row.streamId" icon="el-icon-switch-button" type="text"  style="color: #f56c6c"
                           @click="stopDevicePush(scope.row)">停止
                </el-button>
                <el-button size="medium" v-if="scope.row.pid!=0" icon="el-icon-video-camera" type="text" @click="queryRecords(scope.row)">设备录像
                </el-button>
                <el-button size="medium" v-if="scope.row.pid!=0" icon="el-icon-cloudy"
                           type="text" @click="queryCloudRecords(scope.row)">云端录像
                </el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center" class-name="small-padding fixed-width" width="280">
            <template slot-scope="scope">
              <el-button v-if="scope.row.pid == 0"
                size="mini"
                type="text"
                icon="el-icon-edit"
                @click="handleUpdatePosition(scope.row)"
              >修改</el-button>
              <el-button v-if="scope.row.pid != 0"
                size="mini"
                type="text"
                icon="el-icon-edit"
                @click="handleUpdate(scope.row)"
              >修改</el-button>
              <el-button
                v-if="scope.row.pid == 0"
                size="mini"
                type="text"
                icon="el-icon-plus"
                @click="handleAdd(scope.row)"
              >新增</el-button>
              <el-button
                size="mini"
                type="text"
                icon="el-icon-delete"
                @click="handleDelete(scope.row)"
              >删除</el-button>

            </template>
          </el-table-column>
        </el-table>

        <devicePlayer ref="devicePlayer" ></devicePlayer>
        <cameraEdit ref="cameraEdit"></cameraEdit>
        <cameraPositionEdit ref="cameraPositionEdit"></cameraPositionEdit>
    </div>
</template>

<script>
import devicePlayer from './dialog/devicePlayer.vue'
import uiHeader from '../layout/UiHeader.vue'
import cameraEdit from './dialog/cameraEdit.vue'
import cameraPositionEdit from './dialog/cameraPositionEdit.vue'
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
import {handleTree } from "@/utils/handleTree";
export default {
    name: 'app',
    components: {
        devicePlayer,
        uiHeader,
        cameraEdit,
        cameraPositionEdit,
        Treeselect
    },
    data() {
        return {
            refreshTable: true,
            searchName:'',
            cameraList: [], //设备列表
            currentDevice: {}, //当前操作设备对象
            isExpandAll: true,
            videoComponentList: [],
            updateLooper: 0, //数据刷新轮训标志
            currentDeviceChannelsLenth: 0,
            winHeight: window.innerHeight - 200,
            currentPage: 1,
            count: 15,
            total: 0,
            getCameraListLoading: false,
        };
    },
    computed: {

    },
    mounted() {
        this.getCameraList();
    },
    methods: {
        sendDevicePush: function (itemData) {
          let deviceId = itemData.deviceId;
          this.isLoging = true;
          let channelId = itemData.channelId;
          console.log("通知设备推流1：" + deviceId + " : " + channelId);
          let that = this;
          this.$axios({
            method: 'get',
            url: '/api/play/start/' + deviceId + '/' + channelId,
            params:{
              isSubStream: this.isSubStream
            }
          }).then(function (res) {
            console.log(res)
            that.isLoging = false;
            if (res.data.code === 0) {
              itemData.streamId = res.data.data.stream;
              that.$refs.devicePlayer.openDialog("media", deviceId, channelId, {
                streamInfo: res.data.data,
                hasAudio: itemData.hasAudio
              });
              setTimeout(() => {
                that.getCameraList();
              }, 1000)

            }else{
              that.$message.error(res.data.msg);
            }
          }).catch(function (e) {
            console.error(e)
            that.isLoging = false;
            // that.$message.error("请求超时");
          });
        },
        stopDevicePush: function (itemData) {
          var that = this;
          this.$axios({
            method: 'get',
            url: '/api/play/stop/' + itemData.deviceId + "/" + itemData.channelId,
            params:{
              isSubStream: itemData.isSubStream
            }
          }).then(function (res) {
            that.getCameraList();
          }).catch(function (error) {
            if (error.response.status === 402) { // 已经停止过
              that.getCameraList();
            } else {
              console.log(error)
            }
          });
        },
        queryRecords: function (itemData) {
          let deviceId = itemData.deviceId;
          let channelId = itemData.channelId;

          this.$router.push(`/gbRecordDetail/${deviceId}/${channelId}`)
        },
        queryCloudRecords: function (itemData) {
          let deviceId = itemData.deviceId;
          let channelId = itemData.channelId;

          this.$router.push(`/cloudRecordDetail/rtp/${deviceId}_${channelId}`)
        },
        handleAddPosition(){
          this.$refs.cameraPositionEdit.openDialog(null, () => {
              this.$refs.cameraPositionEdit.close();
              this.$message({
                  showClose: true,
                  message: "添加成功",
                  type: "success",
              });
              setTimeout(this.getCameraList, 200)

          },null)
        },
        handleUpdatePosition(row){
            this.$refs.cameraPositionEdit.openDialog(row, () => {
                this.$refs.cameraPositionEdit.close();
                this.$message({
                    showClose: true,
                    message: "修改成功",
                    type: "success",
                });
                setTimeout(this.getCameraList, 200)

            },row.pid)
        },
        handleDelete(row){
          let msg = "确定删除此摄像头？"
          this.$confirm(msg, '提示', {
              dangerouslyUseHTMLString: true,
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              center: true,
              type: 'warning'
          }).then(() => {
              this.$axios({
                  method: 'post',
                  url: `/api/camera/del`,
                  data: {id:row.id}
              }).then((res) => {
                  this.getCameraList();
              }).catch((error) => {
                  console.error(error);
              });
          }).catch(() => {

          });
        },
        handleUpdate(row){
          this.$refs.cameraEdit.openDialog(row, () => {
              this.$refs.cameraEdit.close();
              this.$message({
                  showClose: true,
                  message: "修改成功",
                  type: "success",
              });
              setTimeout(this.getCameraList, 200)

          },row.pid)
        },
        handleAdd(row){
          this.$refs.cameraEdit.openDialog(null, () => {
              this.$refs.cameraEdit.close();
              this.$message({
                  showClose: true,
                  message: "添加成功",
                  type: "success",
              });
              setTimeout(this.getCameraList, 200)

          },row.id)
        },
        /** 展开/折叠操作 */
        toggleExpandAll() {
          this.refreshTable = false;
          this.isExpandAll = !this.isExpandAll;
          this.$nextTick(() => {
            this.refreshTable = true;
          });
        },
        getCameraList: function () {
            this.getCameraListLoading = true;
            this.$axios({
                method: 'get',
                url: `/api/camera/all`,
                params: {
                    name: this.searchName
                }
            }).then((res) => {
                console.log(res);
                if (res.data.code === 0) {
                    this.total = res.data.data.length;
                    this.cameraList = handleTree(res.data.data, "id","pid");
                    this.loading = false;
                }
                this.getCameraListLoading = false;
            }).catch((error) => {
                console.error(error);
                this.getCameraListLoading = false;
            });
        },
        deleteCamera: function (row) {
            let msg = "确定删除此设备？"
            if (row.online !== 0) {
                msg = "在线设备删除后仍可通过注册再次上线。<br/>如需彻底删除请先将设备离线。<br/><strong>确定删除此设备？</strong>"
            }
            this.$confirm(msg, '提示', {
                dangerouslyUseHTMLString: true,
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                center: true,
                type: 'warning'
            }).then(() => {
                this.$axios({
                    method: 'delete',
                    url: `/api/device/query/devices/${row.deviceId}/delete`
                }).then((res) => {
                    this.getDeviceList();
                }).catch((error) => {
                    console.error(error);
                });
            }).catch(() => {

            });


        },

        edit: function (row) {
            this.$refs.cameraEdit.openDialog(row, () => {
                this.$refs.cameraEdit.close();
                this.$message({
                    showClose: true,
                    message: "设备修改成功，通道字符集将在下次更新生效",
                    type: "success",
                });
                setTimeout(this.getCameraList, 200)

            })
        },
        add: function () {
            this.$refs.cameraEdit.openDialog(null, () => {
                this.$refs.cameraEdit.close();
                this.$message({
                    showClose: true,
                    message: "添加成功",
                    type: "success",
                });
                setTimeout(this.getCameraList, 200)

            })
        }


    }
};
</script>

<style>
.videoList {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
}

.video-item {
    position: relative;
    width: 15rem;
    height: 10rem;
    margin-right: 1rem;
    background-color: #000000;
}

.video-item-img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100%;
}

.video-item-img:after {
    content: "";
    display: inline-block;
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 3rem;
    height: 3rem;
    background-image: url("../assets/loading.png");
    background-size: cover;
    background-color: #000000;
}

.video-item-title {
    position: absolute;
    bottom: 0;
    color: #000000;
    background-color: #ffffff;
    line-height: 1.5rem;
    padding: 0.3rem;
    width: 14.4rem;
}
</style>



